    font-weight: bold;
    line-height: 25px;
    letter-spacing: -0.015em;
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-right: 10px;

    &.active{
        background: #407bff;
    }
}

.pagination-previous{
    transform : rotate(-180deg);
