/*
@import url('https://fonts.googleapis.com/css2?family=Yaldevi:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');
*/
:root {
    --color-primary: #ff8400fd;
    --color-third: #ff840043;
    --primary-color: #ff8400bb;
    --color-quarter: #ff8400bb;
    --color-quarter: #ff8400bb;
    --color-fosco: rgb(255, 255, 255, 0.100);
    --primary-hover-color: #91684483;
    --secondary-hover-color: #ff840043;
    --ml: 50%;
    --ml20: 20px;
    --ml30: 30px;
    --ml40: 40px;
    --ml50: 50px;
    --mb: -3%;
    --color-third: #ff8400fd;
}

.text-primary {
    color: #ff8400fd !important;
    color: var(--color-primary) !important;
}

.text-third {
    color: #ff8400fd !important;
    color: var(--color-primary) !important;
}

.text-quarter {
    color: #ff8400bb !important;
    color: var(--color-quarter) !important;
}

text-third:hover {
    color: #91684483;
    color: var(--primary-hover-color);
}

html,
body {
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

#root {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.mr {
    margin-left: var(--ml2);
    text-decoration: none;
}

.ml-2 {
    margin-left: 20px;
    margin-left: var(--ml20);
    text-decoration: none;
}
.ml-3 {
    margin-left: 30px;
    margin-left: var(--ml30);
    text-decoration: none;
}
.ml-4 {
    margin-left: 40px;
    margin-left: var(--ml40);
    text-decoration: none;
}

.content {
    flex: 1 0 auto;
}

.footer {
    flex-shrink: 0;
    text-align: center;
    font-size: 0.8em;
}

.bg-primary {
    background-color: #ff8400bb !important;
    background-color: var(--primary-color) !important;
}

.bg-third {
    background-color: #ff8400fd !important;
    background-color: var(--color-third) !important;
}

.bg-quarter {
    background-color: #ff8400bb !important;
    background-color: var( --color-quarter) !important;
}

.bg-fosco {
    background-color: rgb(255, 255, 255, 0.100) !important;
    background-color: var( --color-fosco) !important;
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
}

.home-btn-order {
    background-color: #ff8400bb;
    background-color: var(--primary-color);
    border-radius: 30px;
    width: 235px;
    font-weight: bold;
    font-size: 24px;
    letter-spacing: -0.015em;
    color: #FFF;
    margin-top: 50px;
    margin-bottom: 30px;
    text-justify: initial;
}

.home-btn-order:hover {
    background-color: #91684483;
    background-color: var(--primary-hover-color);
    color: #FFF;
    transform: scale(1.01);
}

.spinner {
    display: flex;
    margin-left: 50% !important;
    margin-left: var(--ml) !important;
    margin-bottom: -3% !important;
    margin-bottom: var(--mb) !important;
}

thead,
tfoot {
    background-color: #3f87a6;
    color: #fff;
}

tbody {
    background-color: #e4f0f5;
}

caption {
    padding: 10px;
    caption-side: bottom;
}

table {
    border-collapse: collapse;
    border: 2px solid rgb(200, 200, 200);
    letter-spacing: 1px;
    font-family: sans-serif;
    font-size: .8rem;
}

td,
th {
    border: 1px solid rgb(190, 190, 190);
    padding: 5px 10px;
}

.a {
    text-decoration: none;
    color: #ff8400bb;
    color: var(--primary-color);
}

.a:hover {
    color: #91684483;
    color: var(--primary-hover-color);
}

.base-input {
    border-radius: 10px;
    font-size: 1em;
    letter-spacing: -0.0015em;
    color: #ff8400fd!important;
    color: var(--color-primary)!important;
    height: 50px;
}

.base-input::-webkit-input-placeholder {
    color: #91684483!important;
    color: var(--primary-hover-color)!important;
}

.base-input:-ms-input-placeholder {
    color: #91684483!important;
    color: var(--primary-hover-color)!important;
}

.base-input::placeholder {
    color: #91684483!important;
    color: var(--primary-hover-color)!important;
}

@media (min-width: 576px) {
    .footer {
        flex-shrink: 0;
        text-align: center;
        font-size: 0.8em;
    }
    .container-login {
        margin-top: 20% !important;
    }
}

