.main-nav {
  color: #fff;
  align-items: center;
  padding-left: 40px;
  padding-right: 40px;
  margin-left: 0;
  margin-right: 0;
  justify-content: center;
  display: flex;
}

.nav-container {
  justify-content: left;
  display: flex;
}

.logo {
  margin-left: 5%;
  justify-content: center;
  display: flex;
  display: none;
  align-items: center;
  text-decoration: none;
  color: var(--primary-color);
}

.logo-text {
  text-decoration: none;
  color: var(--primary-color);
  text-transform: uppercase;
  font-size: 14px;
}

.uppercase {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
}

.logo-text:hover {
  color: var(--primary-hover-color);
}

.login {
  justify-content: center;
  display: flex;
}

.nav-username {
  color: var(--secondary-color);
  display: none;
}

@media (min-width: 640px) {
  .nav-username {
    display: unset;
  }
}

@media (min-width: 640px) {
  .logo-text {
    display: unset;
    color: var(--primary-color);
    text-decoration: none;
    text-transform: uppercase;
    font-size: 14px;
  }
}

@media (min-width: 640px) {
  .logo {
    display: unset;
  }
}
